.topic_content_list {
  background-color: #fff;
  width: 90%;
  padding: 0 5%;
}
.topic_content_list .topic_list_tab {
  padding: 1.18518519rem 0;
}
.topic_content_list .topic_list_tab ul li {
  float: left;
  width: 33.333%;
  height: 1.77777778rem;
  text-align: center;
  line-height: 1.77777778rem;
}
.topic_content_list .topic_list_tab ul li a {
  display: block;
  width: 50%;
  font-size: 0.66666667rem;
  margin-left: 25%;
  color: #535353;
}
.topic_content_list .topic_list_tab ul li a.switchClass_active {
  color: #ffc000;
  border-bottom: solid 0.14814815rem #ffc000;
  -webkit-transition: color 0.5s ease-in-out;
  -o-transition: color 0.5s ease-in-out;
  transition: color 0.5s ease-in-out;
}
.topic_content_list .topic_list_item {
  padding: 0.44444444rem 0;
}
.topic_content_list .topic_list_item a {
  display: block;
}
.topic_content_list .topic_list_item a .topic_item_left {
  width: 40%;
  float: left;
}
.topic_content_list .topic_list_item a .topic_item_left img {
  width: 100%;
}
.topic_content_list .topic_list_item a .topic_item_right {
  float: right;
  width: 55%;
  margin-left: 5%;
}
.topic_content_list .topic_list_item a .topic_item_right h3 {
  font-size: 0.74074074rem;
  font-weight: normal;
  color: #ffc000;
  margin-bottom: 0.2962963rem;
}
.topic_content_list .topic_list_item a .topic_item_right p {
  color: #5b5b5b;
  font-size: 0.59259259rem;
}



