.login_wrap {
  background-color: fff;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}
.login_wrap .login_header {
  position: relative;
  height: 2.07407407rem;
  background-color: #fff;
  text-align: center;
  font-size: 0.88888889rem;
  line-height: 2.07407407rem;
}
.login_wrap .login_header div {
  width: 2.96296296rem;
  height: 100%;
  position: absolute;
  top: 0;
  font-size: 0.74074074rem;
}
.login_wrap .login_content {
  width: 90%;
  padding: 0 5%;
  margin: 1.48148148rem 0;
}
.login_wrap .login_content form .login_content_name,
.login_wrap .login_content form .login_content_password {
  width: 100%;
  height: 2.37037037rem;
  line-height: 2.37037037rem;
}
.login_wrap .login_content form .login_content_name label,
.login_wrap .login_content form .login_content_password label {
  font-size: 0.81481481rem;
}
.login_wrap .login_content form .login_content_name input,
.login_wrap .login_content form .login_content_password input {
  outline: none;
  border: none;
  font-size: 0.81481481rem;
  padding-left: 0.59259259rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.login_wrap .login_content form .login_content_option {
  margin: 0.2962963rem 0 1.48148148rem 0;
}
.login_wrap .login_content form .login_content_option button {
  outline: none;
  background-color: #fff;
  border: none;
  font-size: 0.66666667rem;
}
.login_wrap .login_content form .login_content_option .login_option_left {
  float: left;
}
.login_wrap .login_content form .login_content_option .login_option_left button {
  color: #acacac;
}
.login_wrap .login_content form .login_content_option .login_option_right {
  float: right;
}
.login_wrap .login_content form .login_content_option .login_option_right button {
  color: #ffd145;
}
.login_wrap .login_content form .login_content_loginBtn {
  width: 100%;
  height: 2.07407407rem;
  margin: 0.59259259rem 0;
  border-radius: 0.2962963rem;
  color: #fff;
  font-size: 0.74074074rem;
  outline: none;
  border: none;
  background-color: #dddddd;
}
.login_wrap .login_content form .login_content_loginBtn.loginBtn_active {
  background-color: #ffc206;
}
.login_wrap .login_content form .login_content_register {
  width: 100%;
  height: 2.07407407rem;
  margin: 0.59259259rem 0;
  border-radius: 0.2962963rem;
  color: #fff;
  font-size: 0.74074074rem;
  outline: none;
  border: none;
  background-color: #ffdd74;
}
.login_wrap .login_content .login_other_account {
  width: 100%;
  margin-top: 3.7037037rem;
  text-align: center;
}
.login_wrap .login_content .login_other_account h2 {
  font-size: 0.74074074rem;
  color: #acacac;
  font-weight: normal;
}
.login_wrap .login_content .login_other_account ul {
  margin: 1.18518519rem 0;
}
.login_wrap .login_content .login_other_account ul li {
  display: inline-block;
}
.login_wrap .login_content .login_other_account ul li:nth-of-type(1) i {
  color: #50b674;
}
.login_wrap .login_content .login_other_account ul li:nth-of-type(2) i {
  color: #30a5dd;
}
.login_wrap .login_content .login_other_account ul li i {
  display: block;
  width: 2.66666667rem;
  height: 2.66666667rem;
  font-size: 1.77777778rem;
}
.login_wrap .login_content .login_other_account ul li span {
  font-size: 0.59259259rem;
  color: #acacac;
}



