.footer_wrap {
  height: 2.66666667rem;
}
.footer_wrap .footer_nav {
  height: 100%;
}
.footer_wrap .footer_nav .footer_nav_wrap {
  height: 100%;
}
.footer_wrap .footer_nav .footer_nav_wrap .footer_nav_item {
  float: left;
  width: 20%;
  text-align: center;
  height: 100%;
}
.footer_wrap .footer_nav .footer_nav_wrap .footer_nav_item a {
  display: block;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 0.74074074rem;
  color: #747474;
  -webkit-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}
.footer_wrap .footer_nav .footer_nav_wrap .footer_nav_item a.nav_active {
  -webkit-animation: nav_scale 0.5s ease-in-out forwards;
          animation: nav_scale 0.5s ease-in-out forwards;
}
.footer_wrap .footer_nav .footer_nav_wrap .footer_nav_item a.nav_active i {
  -webkit-animation: nav_rotate 0.5s ease-out forwards 0.3s;
          animation: nav_rotate 0.5s ease-out forwards 0.3s;
}
.footer_wrap .footer_nav .footer_nav_wrap .footer_nav_item a i {
  display: block;
  width: 100%;
  height: 0.74074074rem;
  font-size: 0.74074074rem;
  font-weight: bold;
  margin-bottom: 0.14814815rem;
}
.footer_wrap .footer_nav .footer_nav_wrap .footer_nav_item a p {
  font-size: 0.59259259rem;
}
@-webkit-keyframes nav_scale {
  form {
    color: #747474;
  }
  to {
    color: #ffc206;
  }
}
@keyframes nav_scale {
  form {
    color: #747474;
  }
  to {
    color: #ffc206;
  }
}
@-webkit-keyframes nav_rotate {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  25% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  75% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes nav_rotate {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  25% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  75% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



