.createTheme {
  visibility: hidden;
  z-index: 100;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
}
.createTheme.create_active {
  visibility: visible;
  -webkit-animation: create_bgc 0.3s ease-in-out forwards;
          animation: create_bgc 0.3s ease-in-out forwards;
}
.createTheme.create_active .createTheme_wrap {
  -webkit-animation: create_height 0.3s ease-in-out forwards;
          animation: create_height 0.3s ease-in-out forwards;
}



