.createTheme_wrap {
  width: 100%;
  height: 0%;
  overflow: hidden;
  margin-top: 2.07407407rem;
}
.createTheme_wrap .createTheme_arrow {
  height: 0;
  width: 0;
  margin-left: 5%;
  border-left: 0.2962963rem solid transparent;
  border-right: 0.2962963rem solid transparent;
  border-bottom: 0.44444444rem solid rgba(255, 255, 255, 0.9);
}
.createTheme_wrap .createTheme_box {
  width: 96%;
  margin-left: 2%;
  background-color: #fff;
  border-radius: 0.2962963rem;
  background-color: #ffffff;
}
.createTheme_wrap .createTheme_box .createTheme_box_left,
.createTheme_wrap .createTheme_box .createTheme_box_right {
  float: left;
  width: 50%;
  height: 100%;
  text-align: center;
  line-height: 4.44444444rem;
  color: #696969;
}
.createTheme_wrap .createTheme_box .createTheme_box_left div,
.createTheme_wrap .createTheme_box .createTheme_box_right div {
  display: inline-block;
  width: 1.48148148rem;
  height: 1.48148148rem;
  border-radius: 50%;
  text-align: center;
  line-height: 1.48148148rem;
  margin-right: 0.44444444rem;
}
.createTheme_wrap .createTheme_box .createTheme_box_left div i,
.createTheme_wrap .createTheme_box .createTheme_box_right div i {
  color: #fff;
  font-size: 0.59259259rem;
}
.createTheme_wrap .createTheme_box .createTheme_box_left span,
.createTheme_wrap .createTheme_box .createTheme_box_right span {
  font-size: 0.59259259rem;
}
.createTheme_wrap .createTheme_box .createTheme_box_left div {
  background-color: #ffd555;
}
.createTheme_wrap .createTheme_box .createTheme_box_right div {
  background-color: #7cc5fb;
}
@-webkit-keyframes create_height {
  from {
    height: 0%;
  }
  to {
    height: 5.18518519rem;
  }
}
@keyframes create_height {
  from {
    height: 0%;
  }
  to {
    height: 5.18518519rem;
  }
}
@-webkit-keyframes create_bgc {
  form {
    background-color: rgba(0, 0, 0, 0);
  }
  to {
    background-color: rgba(0, 0, 0, 0.4);
  }
}
@keyframes create_bgc {
  form {
    background-color: rgba(0, 0, 0, 0);
  }
  to {
    background-color: rgba(0, 0, 0, 0.4);
  }
}



